/*------------------------------------*\
      $MAIN
    \*------------------------------------*/

body {
  margin: 0 auto;
  width: 90%;
  max-width: 1240px;
  font-family: "Roboto", sans-serif;
  background-color: #f6f6f6;
}

/* Typography */

h1 {
  font-size: 28px;
  font-weight: 300;
  flex: 1 1;
}

h5 {
  font-weight: 500;
  line-height: 1.7em;
}

h6 {
  color: #666;
  font-size: 14px;
}

/* Product Layout */

.product-filter {
  display: flex;
  padding: 30px 0;
}

.sort {
  display: flex;
  align-self: flex-end;
}

.collection-sort {
  display: flex;
  flex-direction: column;
}

.collection-sort:first-child {
  padding-right: 20px;
}

label {
  color: #666;
  font-size: 10px;
  font-weight: 500;
  line-height: 2em;
  text-transform: uppercase;
}

.products {
  display: flex;
  flex-wrap: wrap;
}
.product-card:hover {
  color: red;
}
.sale,
.product-card {
  display: flex;
  flex-direction: column;

  padding: 2%;
  flex: 1 1 16%;

  background-color: #fff;
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.25);
}

.product-image img {
  width: 100%;
}

.product-info {
  margin-top: auto;
  padding-top: 20px;
  text-align: center;
}

@media (max-width: 920px) {
  .product-card {
    flex: 1 1 21%;
  }
  .sale,
  .products .product-card:first-child,
  .products .product-card:nth-child(2) {
    flex: 2 1 46%;
  }
}

@media (max-width: 600px) {
  .sale,
  .product-card {
    flex: 1 1 46%;
  }
}

@media (max-width: 480px) {
  h1 {
    margin-bottom: 20px;
  }

  .product-filter {
    flex-direction: column;
  }

  .sort {
    align-self: flex-start;
  }
}

.days {
  /* border-color: rgb(0, 0, 0); */
  color: rgb(255, 0, 0);
  background-color: rgb(255, 255, 255);
  text-align: center;
  font-size: 7vw;
  margin: auto;
  margin-bottom: 2%;
  border-style: none;
}
.imgSale {
  border-radius: 20px;
  border-style: solid;
  border-width: 10px;
  width: 35%;
  color: rgb(255, 230, 0);
  height: auto;
  margin: auto;
}
.sale div div {
  border-radius: 20px;
  border-style: solid;
  border-width: 2px;
  color: black;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background-color: red;
  position: relative;
  animation-name: example;
  animation-duration: 1s;
}
.sale div {
  /* border-radius: 20px; */
  /* border-style: solid; */
  border-width: 2px;
  color: black;
}
.sale {
  border-radius: 20px;
  border-style: solid;
  border-width: 2px;
  color: black;
}
@keyframes example {
  0% {
    background-color: rgb(94, 255, 0);
    right: 0px;
    top: 0px;
  }
  100% {
    background-color: red;
    right: 98%;
    top: 0px;
  }
}
.sale {
  border-radius: 20px;
  border-style: solid;
  border-width: 10px;
  border-color: rgb(0, 0, 0);
  color: rgb(255, 0, 0);
  background-color: rgb(255, 255, 255);
  text-align: center;
  font-size: 7vw;
  margin: auto;
  margin-bottom: 2%;
